import React, { useState } from "react";
import { Button, Container, Row, Col, Form } from "react-bootstrap";
import PageWrapper from "./../components/page-wrapper";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPaperPlane } from "@fortawesome/free-solid-svg-icons";
import { withState } from "./../Context";

const encode = (data) => {
  return Object.keys(data)
    .map((key) => encodeURIComponent(key) + "=" + encodeURIComponent(data[key]))
    .join("&");
};

const ContactUsPage = withState(
  ({
    state: {
      translations: { t },
      name: pageName,
      address,
      phone
    }
  }) => {
    const [form, setForm] = useState({ email: "", name: "", message: "" });

    const handleSubmit = (e) => {
      fetch("/", {
        method: "POST",
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        body: encode({ "form-name": "contact-us", ...form })
      })
        .then((res) => alert("Success!")) // TODO redirect to thank you page, or show success block above form
        .catch((error) => alert(error));

      e.preventDefault();
    };

    const handleChange = (e) => {
      setForm((state) => ({ ...state, [e.target.name]: e.target.value }));
    };
    const { email, name, message } = form;

    return (
      <PageWrapper hasNav>
        <section className="content-section py-3 py-md-5">
          <Container>
            <Row>
              <Col sm={4}>
                {pageName && <h2>{pageName}</h2>}

                {address && (
                  <>
                    <h5>{t("contactUsPageAddress")}</h5>
                    <p>
                      {address.street}
                      <br />
                      {address.post}
                      <br />
                      {address.country}
                    </p>
                  </>
                )}

                {phone && (
                  <>
                    <h5>{t("contactUsPagePhone")}</h5>
                    <p>
                      <a href={`tel:${phone.replace(/[\s()]/g, "")}`}>
                        {phone}
                      </a>
                    </p>
                  </>
                )}
              </Col>
              <Col sm={7} style={{ borderLeft: "1px solid #e6e6e6" }}>
                <div className="contact-form-container">
                  <p
                    className="alert-message warning validation"
                    style={{ display: "none" }}
                  >
                    <i className="ico fa fa-exclamation-circle"></i>
                    <strong>All fields are required!</strong>
                  </p>
                  <p
                    className="alert-message warning request"
                    style={{ display: "none" }}
                  >
                    <i className="ico fa fa-exclamation-circle"></i>
                    <strong>Form not sent!</strong>
                    <br />
                    There was a connection problem. Try again later.
                  </p>

                  <Form
                    onSubmit={handleSubmit}
                    className="px-md-5 pb-md-4"
                    data-netlify-recaptcha="true"
                  >
                    <h5 className="pb-3">{t("contactUsPageSendUsAMessage")}</h5>

                    <Row>
                      <Form.Group as={Col} sm={6}>
                        <Form.Label className="ml-2">
                          {t("contactUsPageYourName")}
                        </Form.Label>
                        <Form.Control
                          type="text"
                          placeholder={t("contactUsPageNamePlaceholder")}
                          required
                          name="name"
                          value={name}
                          onChange={(e) => handleChange(e)}
                        />
                      </Form.Group>
                      <Form.Group as={Col} sm={6}>
                        <Form.Label className="ml-2">
                          {t("contactUsPageYourEmail")}
                        </Form.Label>
                        <Form.Control
                          type="email"
                          name="email"
                          required
                          placeholder="email@domain.com"
                          value={email}
                          onChange={(e) => handleChange(e)}
                        />
                      </Form.Group>
                      <Form.Group as={Col} sm={12}>
                        <Form.Label className="ml-2">
                          {t("contactUsPageYourMessage")}
                        </Form.Label>
                        <Form.Control
                          as="textarea"
                          rows={3}
                          required
                          name="message"
                          value={message}
                          onChange={(e) => handleChange(e)}
                        />
                      </Form.Group>
                    </Row>

                    <Row>
                      <Col>
                        <Button type="submit" className="ml-auto">
                          <FontAwesomeIcon
                            icon={faPaperPlane}
                            size={"sm"}
                            className={"mr-2"}
                          />
                          {t("contactUsPageCtaSend")}
                        </Button>
                      </Col>
                    </Row>
                  </Form>
                </div>
              </Col>
            </Row>
          </Container>
        </section>
      </PageWrapper>
    );
  }
);

export default ContactUsPage;
